.header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: var(--body-color);
}

.nav{
    height: calc(var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
}

.nav__logo,
.nav__toggle{
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav__list{
    display: flex;
    column-gap: 2rem; 
}

.nav__link{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--title-color);
    font-size: var(--small-font-size);
    font-weight: var(--font-medium);
    transition: .3s;
}


.nav__icon,
.nav__close,
.nav__toggle{
    display: none;
}   


/* Active link*/
.active-link,
.nav__link:hover{
    color: var(--title-color-dark);
}

/*=============== BREAKPOINTS ===============*/
/* For medium devices */
@media screen and (max-width: 768px) {
    .header{
        top: initial;
        bottom: 0;
    }

    .nav{
        height: var(--header-height);
    }

    .nav__menu{
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--body-color);
        box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
        padding: 2rem 1.5rem 4rem;
        border-radius: 1.5rem 1.5rem 0 0;
        transition: .3s;

    }

    /* Show Menu*/
    .show-menu{
        bottom: 0;
    }

    .nav__list{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
    }

    .nav__link{
        font-size: var(--h3-font-size);
    }

    .nav__close{
        position: absolute;
        /* top: 1.5rem;
        right: 1.5rem;   above
        font-size: 2rem; */
        cursor: pointer;

        /* bottom */
        right: 1.3rem;
        bottom: .5rem;     
        font-size: 1.5rem;
        color: var(--title-color);
    }

    .nav__close:hover{
        color: var(--title-color-dark);
    }

    .nav__toggle{
        font-size: 1.1rem;
        cursor: pointer;
    }

    .nav__icon,
    .nav__close,
    .nav__toggle{
        display: block;
        font-size: 1.5rem;
        cursor: pointer;
    }
}
